.Home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.Home h1 {
  font-size: 6rem;
  font-family: monospace;
  color: #FF3D00;
}

.Home div btn:first-child {
  margin-right: 10px;
}

.HomeBG {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -2;
  opacity: 0.9;
  height: 100%;
  width: 100%;
}

.HomeOverlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.3);
}


